import { useContext, useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { TravelContext } from "../context/TravelContext";

function EditTravel() {
  const { travels, setTravels } = useContext(TravelContext);
  const { destination } = useParams();
  
  const [formData, setFormData] = useState({
    destination: "",
    date: "",
    group: ""
  });

  useEffect(() => {
    const currentTravel = travels.find(t => t.destination === destination);
    if (currentTravel) {
      setFormData({
        destination: currentTravel.destination,
        date: currentTravel.date,
        group: currentTravel.group
      });
    }
  }, [destination, travels]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value
    }));
  };

  const saveChanges = (e) => {
    e.preventDefault(); // Prevent default form submission
    const updatedTravels = travels.map(travel => 
      travel.destination === destination ? { ...travel, ...formData } : travel
    );
    
    setTravels(updatedTravels);
  };

  return (
    <div style={{
      maxWidth: '400px',
      margin: '50px auto',
      padding: '20px',
      border: '1px solid #ccc',
      borderRadius: '8px',
      boxShadow: '0 2px 10px rgba(0, 0, 0, 0.2)',
      backgroundColor: '#f8f9fa', // Light gray background
      fontFamily: 'Arial, sans-serif'
    }}>
      <h2 style={{
        textAlign: 'center',
        color: '#343a40', // Dark gray text
        marginBottom: '20px'
      }}>Edit Travel</h2>
      <form onSubmit={saveChanges}>
        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="destination" style={{
            display: 'block',
            marginBottom: '5px',
            color: '#6c757d' // Medium gray
          }}>Destination:</label>
          <input 
            type="text" 
            id="destination"
            name="destination"
            value={formData.destination} 
            onChange={handleChange} 
            required
            style={{
              width: '100%',
              padding: '10px',
              border: '1px solid #ced4da', // Light gray border
              borderRadius: '4px',
              boxSizing: 'border-box',
              fontSize: '16px',
              backgroundColor: '#ffffff' // White background for input
            }}
          />
        </div>
        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="date" style={{
            display: 'block',
            marginBottom: '5px',
            color: '#6c757d' // Medium gray
          }}>Date:</label>
          <input 
            type="date"
            id="date"
            name="date"
            value={formData.date} 
            onChange={handleChange} 
            required
            style={{
              width: '100%',
              padding: '10px',
              border: '1px solid #ced4da', // Light gray border
              borderRadius: '4px',
              boxSizing: 'border-box',
              fontSize: '16px',
              backgroundColor: '#ffffff' // White background for input
            }}
          />
        </div>
        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="group" style={{
            display: 'block',
            marginBottom: '5px',
            color: '#6c757d' // Medium gray
          }}>Group:</label>
          <input 
            type="text" 
            id="group"
            name="group"
            value={formData.group} 
            onChange={handleChange} 
            required
            style={{
              width: '100%',
              padding: '10px',
              border: '1px solid #ced4da', // Light gray border
              borderRadius: '4px',
              boxSizing: 'border-box',
              fontSize: '16px',
              backgroundColor: '#ffffff' // White background for input
            }}
          />
        </div>
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          marginTop: '20px'
        }}>
          <button type="submit" style={{
            backgroundColor: '#6c757d', // Medium gray background
            color: '#ffffff',
            border: 'none',
            padding: '10px 15px',
            borderRadius: '4px',
            cursor: 'pointer',
            transition: 'background-color 0.3s',
            boxShadow: '0 2px 5px rgba(0, 0, 0, 0.2)',
          }} onMouseOver={(e) => e.currentTarget.style.backgroundColor = '#5a6268'} onMouseOut={(e) => e.currentTarget.style.backgroundColor = '#6c757d'}>
            Save Changes
          </button>
          <Link to="/" style={{
            textDecoration: 'none',
            color: '#007bff',
            padding: '10px 15px',
            borderRadius: '4px',
            border: '1px solid #007bff',
            display: 'inline-block',
            transition: 'background-color 0.3s',
          }} onMouseOver={(e) => e.currentTarget.style.backgroundColor = '#0056b3'} onMouseOut={(e) => e.currentTarget.style.backgroundColor = 'transparent'}>
            Cancel
          </Link>
        </div>
      </form>
    </div>
  );
}

export default EditTravel;